<template>
  <div id="search">
    <el-row :gutter="20">
      <el-col
        v-for="(item, i) in search "
        :key="i"
        :span="item.span"
        class="flex flex-align-center"
      >
        <!-- input输入框 -->
        <el-input
          v-if="item.type === 'input'"
          class="flex-1"
          v-model="searchValCopy[item.key]"
          :placeholder="item.placeholder"
          clearable
        >
        </el-input>
        <!-- date日期选择器-带快捷方式 -->
        <el-date-picker
          v-else-if="item.type === 'date-picker-time'"
          v-model="searchValCopy[item.key]"
          value-format="yyyy-MM-dd hh:mm:ss"
          type="datetime"
          :placeholder="item.placeholder"
          :picker-options="pickerOptions"
        >
        </el-date-picker>
        <!-- date日期选择器 -->
        <el-date-picker
          v-else-if="item.type === 'date-picker'"
          v-model="searchValCopy[item.key]"
          value-format="yyyy-MM-dd hh:mm:ss"
          type="date"
          :placeholder="item.placeholder"
        >
        </el-date-picker>
        <!-- date-range日期范围选择器 -->
        <el-date-picker
          v-else-if="item.type === 'date-picker-range'"
          v-model="searchValCopy[item.key]"
          value-format="yyyy-MM-dd hh:mm:ss"
          type="datetimerange"
          :start-placeholder="item.startPlaceholder"
          :end-placeholder="item.endPlaceholder"
        >
        </el-date-picker>
        <!-- select选择器 -->
        <el-select
          v-else-if="item.type === 'select'"
          v-model="searchValCopy[item.key]"
          :placeholder="item.placeholder"
        >
          <el-option
            v-for="item in item.options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-col>
    </el-row>
    <!-- 搜索功能 -->
    <el-row id="btnList" class="flex flex-pack-end">
      <el-button icon="el-icon-refresh-left" @click="restData">重置</el-button>
      <el-button type="primary" icon="el-icon-search" @click="searchData">搜索</el-button>
    </el-row>
  </div>
</template>

<script>
import { deepClone, debounce } from '../../utils'

export default {
  name: 'search',
  props: {
    search: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      searchVal: {},//搜索关键词
      searchValCopy: {},//搜索关键词备份
      isSearch: false,//判断是否搜索了
      pickerOptions: {
        shortcuts: [{
          text: '今天',
          onClick(picker) {
            picker.$emit('pick', new Date())
          }
        }, {
          text: '昨天',
          onClick(picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24)
            picker.$emit('pick', date)
          }
        }, {
          text: '一周前',
          onClick(picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', date)
          }
        }, {
          text: '三月前',
          onClick(picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 30 * 3)
            picker.$emit('pick', date)
          }
        }, {
          text: '半年前',
          onClick(picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 30 * 6)
            picker.$emit('pick', date)
          }
        }, {
          text: '一年前',
          onClick(picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 30 * 12)
            picker.$emit('pick', date)
          }
        }]
      },//日期快捷选择
      debounceFn: null//保存函数的地址，确保每次watch都只调用这个方法
    }
  },
  watch: {
    searchValCopy: {
      deep: true,
      handler() {
        if (!this.debounceFn) {
          this.createDebounceFn() // 首次使用时生成函数
        }
        this.debounceFn()
      }
    }
  },
  methods: {
    createDebounceFn() {
      this.debounceFn = debounce(() => {
        let data = this.searchValCopy
        for (let i in data) {
          if (data[i] === '' || data[i] === null) {
            delete data[i]
          }
        }
      }, 500)
    },
    restData() {//重置搜索
      this.searchValCopy = {}
      this.isSearch = false
      this.$emit('restData')
    },
    searchData() {//搜索数据
      if (JSON.stringify(this.searchValCopy) === '{}') {
        this.$message.error('请输入关键词再进行搜索')
        return
      }
      this.isSearch = true
      this.searchVal = deepClone(this.searchValCopy)
      this.$emit('searchData')
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.el-select {
  width: 100%;
}

.el-date-editor {
  width: 100%;
}

#search {
  display flex
  justify-content space-between
  margin-top 20px
  margin-bottom 10px
  .el-row:first-child {
    width calc(100% - 200px)
  }
}
</style>



